.container-Home{
    text-align: center;
}
.img-home{
    height: 80px;
    padding-top: 50px;
    padding-bottom: 50px;
}
.Body-Home{
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr ;
    gap: 1rem;
    margin-left: 20px;
    margin-right: 20px;
}
.objeck-render{
    align-items: center;
}
.Body-Home button{
    min-width: 150px;
    min-height: 50px;
    border-radius: 10px;
    font-weight: bold;
    padding-top: 3px;
    padding-bottom: 3px;
    border:none;
    background-color: white;
}
.objeck-render input{
    min-width: 120px;
    border-radius: 10px;
}
.lcd{
    height: 100px;
    width: 40%;
    margin-bottom: 20px;
    background-color: white;
    border-radius: 10px;
    margin-left: 30%;
    margin-right: 30%;
}